@import url(https://fonts.googleapis.com/css2?family=Ubuntu+Mono&display=swap);
:root {
  /* Colors */
  --green: #a9e1af;
  --green-transparent: #a9e1af56;
  --green-lighter: #dcf5df;
  --green-darker: #87b88c;
  --brown: #bb8867;
  --brown-darker: #83624c;
  --white: #eeeef0;
  --white-lighter: #efefef;
  --white-light: #f5f5f7;
  --black: #201f1fd7;

  /* Effects */
  --hover-inside-sm: 0.98;
  --hover-outside-sm: 1.05;
  --boder-radius-sm: 0.3em;
}

.App {
  font-family: "Ubuntu Mono", monospace;
}

.info {
  box-shadow: 0 -1px 5px rgba(0, 0, 0, 0.199);
  border-radius: var(--border-radius-sm);
  transition: all 0.5s ease;
  -webkit-backface-visibility: hidden;
          backface-visibility: hidden;
  border-radius: 0.3em;
  border-radius: var(--boder-radius-sm);
}

.info:hover {
  -webkit-transform: scale(1.05);
  -webkit-transform: scale(var(--hover-outside-sm));
  transform: scale(1.05);
  transform: scale(var(--hover-outside-sm));
}

.green-transparent {
  background: #a9e1af56;
  background: var(--green-transparent);
  background-color: #a9e1af56;
  background-color: var(--green-transparent);
}

.background {
  z-index: -10;
  background: #a9e1af;
  background: var(--green); /* FF3.6+ */ /* Chrome,Safari4+ */ /* Chrome10+,Safari5.1+ */ /* Opera 11.10+ */ /* IE10+ */
  background: linear-gradient(
    to bottom,
    #a9e1af 0%,
    #dcf5df 100%
  );
  background: linear-gradient(
    to bottom,
    var(--green) 0%,
    var(--green-lighter) 100%
  ); /* W3C */
  filter: progid:DXImageTransform.Microsoft.gradient( startColorstr=var(--green), endColorstr=var(--green-lighter),GradientType=0 ); /* IE6-9 */
}

.paper {
  border-radius: var(--border-radius-sm);
  background: #eeeef0;
  background: var(--white); /* FF3.6+ */ /* Chrome,Safari4+ */ /* Chrome10+,Safari5.1+ */ /* Opera 11.10+ */ /* IE10+ */
  background: linear-gradient(
    to bottom,
    #eeeef0 0%,
    #efefef 100%
  );
  background: linear-gradient(
    to bottom,
    var(--white) 0%,
    var(--white-lighter) 100%
  ); /* W3C */
  filter: progid:DXImageTransform.Microsoft.gradient( startColorstr=var(--green), endColorstr=var(--green-lighter),GradientType=0 ); /* IE6-9 */

  border-radius: 8px;
  border-left: 2px solid #f5f5f7;
  border-bottom: 2px solid #f5f5f7;
  border-right: 1px solid #87b88c;
  border-right: 1px solid var(--green-darker);
  border-top: 1px solid #f4f4f6;

  box-shadow: 0 0 2px 0 #f4f4f6 inset,
    0em 100px 500px -5px rgba(255, 255, 255, 0.5),
    -1em 1em 3em 1em rgba(255, 255, 255, 0.2),
    -5em 1em 30em 1em rgba(255, 255, 255, 0.2),
    0em 2px 0 rgba(255, 255, 255, 0.7),
    0em 10px 50px -10px rgba(255, 255, 255, 1),
    1.2em -0.8em 2em 0em #87b88c,
    5.5em -1em 50em 0em #87b88c,
    0px -5px 25px 0px #dcf5df;

  box-shadow: 0 0 2px 0 #f4f4f6 inset,
    0em 100px 500px -5px rgba(255, 255, 255, 0.5),
    -1em 1em 3em 1em rgba(255, 255, 255, 0.2),
    -5em 1em 30em 1em rgba(255, 255, 255, 0.2),
    0em 2px 0 rgba(255, 255, 255, 0.7),
    0em 10px 50px -10px rgba(255, 255, 255, 1),
    1.2em -0.8em 2em 0em var(--green-darker),
    5.5em -1em 50em 0em var(--green-darker),
    0px -5px 25px 0px var(--green-lighter);

  /* -webkit-transition: all 0.5s ease;
  -moz-transition: all 0.5s ease;
  -ms-transition: all 0.5s ease;
  -o-transition: all 0.5s ease;
  transition: all 0.5s ease;
  backface-visibility: hidden; */
}

/* .paper:hover {
  -webkit-transform: scale(var(--hover-inside-sm));
  -moz-transform: scale(var(--hover-inside-sm));
  -ms-transform: scale(var(--hover-inside-sm));
  -o-transform: scale(var(--hover-inside-sm));
  transform: scale(var(--hover-inside-sm));
} */

h1 {
  margin: 0.5em auto;
  color: #201f1fd7;
  color: var(--black);
  text-shadow: 0 0px 20px #eee;
}

h2,
h3,
h4,
h5 {
  color: #bb8867;
  color: var(--brown);
}

p,
ul,
li {
  font-size: 1em;
  color: #201f1fd7;
  color: var(--black);
  text-align: justify;
}

a {
  text-decoration: none;
  color: #ff0066;
  transition: all 0.5s ease;
  border-bottom: 1px solid white;
}

a:hover {
  border-bottom: 1px solid black;
}

body {
  font-family: "Ubuntu Mono", monospace;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.text--black {
  color: black;
}

.wrapped {
  word-wrap: break-word;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.terminal {
  background: var(--green);
  background-color: var(--green);
  box-shadow:
      /* The top layer shadow */ 0 -1px 5px rgba(0, 0, 0, 0.15),
    /* The second layer */ 12px 20px 25px -5px var(--green-darker),
    /* The second layer shadow */ 12px 8px 1px 0px rgba(0, 0, 0, 0.15);
  /* Padding for demo purposes */
  /* padding: 1em; */
  border-color: var(--brown);
  border-radius: var(--boder-radius-sm);
}

.avatar {
  box-shadow: 10px 10px 1px -4px rgba(0, 0, 0, 0.15);
}

.avatar,
.terminal {
  transition: all 0.5s ease;
  -webkit-backface-visibility: hidden;
          backface-visibility: hidden;
}

.terminal:hover,
.avatar:hover {
  -webkit-transform: scale(var(--hover-outside-sm));
  transform: scale(var(--hover-outside-sm));
}

