  -moz-transform: scale(var(--hover-inside-sm));
  -ms-transform: scale(var(--hover-inside-sm));
  -o-transform: scale(var(--hover-inside-sm));
  transform: scale(var(--hover-inside-sm));
} */

h1 {
  margin: 0.5em auto;
  color: var(--black);
  text-shadow: 0 0px 20px #eee;
}

h2,
h3,
h4,
h5 {
  color: var(--brown);
}

p,
ul,
li {
  font-size: 1em;
  color: var(--black);
  text-align: justify;
}

a {
  text-decoration: none;
  color: #ff0066;
  transition: all 0.5s ease;
  border-bottom: 1px solid white;
}

a:hover {
  border-bottom: 1px solid black;
}

body {
  font-family: "Ubuntu Mono", monospace;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.text--black {
  color: black;
}

.wrapped {
  word-wrap: break-word;
}
